var s;
var Renderer = {
        settings: {
        },
        init: function(){
            s = this.settings;
            
        },
        createTerrain: function(imgUrl,isRepeat) {
            s.background = s.ctx.createPattern(resources.get(imgUrl), isRepeat);
        },

        createCanvas: function() {
            // Create the canvas
            s.canvas = document.createElement("canvas");
            s.canvas.id = "gameCanvas";
            s.canvas.classList.add("gameCanvas");
            s.ctx = s.canvas.getContext("2d");
            s.canvas.width = 640;
            s.canvas.height = 480;
            document.querySelector('#game').appendChild(s.canvas);
        },

        renderEntities: function(list,dt) {
            for(let i=0; i<list.length; i++) {
                Renderer.renderEntity(list[i],dt);
            }
        },
        renderEntity: function(entity, dt) {
            if(entity.sprite != null){   
                s.ctx.save();
                s.ctx.translate(entity.pos[0], entity.pos[1]);
                entity.sprite.render(s.ctx,dt);
                s.ctx.restore();
            }
        },
        // Draw everything
        renderAll: function(state, entities, dt) {
            s.ctx.fillStyle = s.background;
            s.ctx.fillRect(0, 0, s.canvas.width, s.canvas.height);

            // Render the player if the game isn't over
                this.renderEntities(entities, dt);
        }
}
Renderer.init();

window.Renderer = Renderer;