import {Entity, BodyParticle, Body, Head, LArm, RArm, Legs } from '../components/entities';

export class Humanoid extends Entity {
    constructor (pos) {
        super();
            this.pos = pos||[160,340];
            this.sprIndex=3;
                                //  ajustment      framePos frameSize in sprite sheet
            this.rArm = new RArm( [3,1],          [300,80*this.sprIndex],[60,80], this);
            this.legs = new Legs( [-20,24],      [360,80*this.sprIndex],[60,80], this);
            this.body = new Body( [-13,-3],     [180,80*this.sprIndex],[60,80], this);
            this.lArm = new LArm( [-17,1],       [240,80*this.sprIndex],[60,80], this);
            this.head = new Head( [-30,-73],    [0,80*this.sprIndex],[60,80], this);
            
    }
}

class Player extends Humanoid{
    constructor(pos,sprite) {
        super(pos);
        this.name = 'enemy';
        this.sprite = null;
        this.bodyPartMoveTime =0.4;
    
        this.idleAnim = new Animation('idle',this.idle());
        // this.attackAnim = new Animation();
        this.animator = new Animator([this.idleAnim]);
    }
    idle(){
        // this.head, tempProgress,  max, direction, time, repStyle(once,repeat,bounce)
        return [[this.head, 0, 1 , [0,-1], this.bodyPartMoveTime, 'bounce',1],
            [this.body, 0, 1, [0,1], this.bodyPartMoveTime,'bounce',1],
            [this.lArm, 0, 1, [-1,1], this.bodyPartMoveTime, 'bounce',1],
            [this.rArm, 0, 0.5, [1,1], this.bodyPartMoveTime, 'bounce',1],
            [this.legs, 0, 1, [1,0], this.bodyPartMoveTime,'bounce',1]];
    }
    // update(e,dt,entities){
    // }
}

export {Player as default} 