import '../components/resources.js';
import '../components/sprite.js';
import '../components/animator.js';
import '../components/input.js';
import '../components/renderer.js';
import '../components/collision.js';
import  '../components/events.js';
import '../components/vector.js';
import LevelManager from '../managers/LevelManager';
import Player from '../models/Player';
import {Enemy} from '../components/entities';
import { ENETDOWN } from 'constants';


Renderer.createCanvas();

var ctx = Renderer.settings.ctx;
var canvas = Renderer.settings.canvas;

var requestAnimFrame = window.requestAnimationFrame;

// The main game loop
var lastTime;
events.on('updateEachFrame', update);
function loop() {
    var now = Date.now();
    var dt = (now - lastTime) / 1000.0;
    events.trigger('renderEachFrame',dt);
    events.trigger('updateEachFrame',dt,entities);
    Renderer.renderAll(isGameOver, entities, dt);
    lastTime = now;
    requestAnimFrame(loop);
};

function init() {
    Renderer.createTerrain('img/terrain.jpg','no-repeat');
    document.getElementById('play-again').addEventListener('click', function() {  LevelManager.restart(); });

    lastTime = Date.now();
    loop();
};

resources.load([
    'img/entities.png',
    'img/attacks.png',
    'img/explosions.png',
    'img/terrain.jpg'
]);

document.getElementById('play').addEventListener('click', function() { 
    if(resources.isReady())
        init(); 
        LevelManager.start(); 
});

window.entities = [];

var enemy = new Enemy();
var player = new Player();

var bullets = [];
var enemies = [];
var explosions = [];

var lastFire = Date.now();
var gameTime = LevelManager.settings.gameTime;
var isGameOver = LevelManager.settings.isGameOver;



// Speed in pixels per second
var bulletSpeed = 500;
var enemySpeed = 100;



// Update game objects
function update(e, dt, entities) {
    gameTime += dt;

    handleInput(dt);
    updateEntities(dt, entities);


    Collision.checkCollisions(entities);

    LevelManager.displayScore();
};



function handleInput(dt) {
    if(input.isDown('DOWN') || input.isDown('s')) {
        player.pos[1] += player.speed * dt;
    }

    if(input.isDown('UP') || input.isDown('w')) {
        player.pos[1] -= player.speed * dt;
    }

    if(input.isDown('LEFT') || input.isDown('a')) {
        player.pos[0] -= player.speed * dt;
    }

    if(input.isDown('RIGHT') || input.isDown('d')) {
        player.pos[0] += player.Speed * dt;
    }

    if(input.isDown('SPACE') &&
       !isGameOver &&
       Date.now() - lastFire > 100) {
        var x = player.pos[0] + player.sprite.size[0] / 2;
        var y = player.pos[1] + player.sprite.size[1] / 2;

        bullets.push({ pos: [x, y],
                       dir: 'forward',
                       sprite: new Sprite('img/sprites.png', [0, 39], [18, 8]) });
        bullets.push({ pos: [x, y],
                       dir: 'up',
                       sprite: new Sprite('img/sprites.png', [0, 50], [9, 5]) });
        bullets.push({ pos: [x, y],
                       dir: 'down',
                       sprite: new Sprite('img/sprites.png', [0, 60], [9, 5]) });

        lastFire = Date.now();
    }
}

function updateEntities(dt) {

    // Update all the bullets
    for(var i=0; i<bullets.length; i++) {
        var bullet = bullets[i];

        switch(bullet.dir) {
        case 'up': bullet.pos[1] -= bulletSpeed * dt; break;
        case 'down': bullet.pos[1] += bulletSpeed * dt; break;
        default:
            bullet.pos[0] += bulletSpeed * dt;
        }

        // Remove the bullet if it goes offscreen
        if(bullet.pos[1] < 0 || bullet.pos[1] > canvas.height ||
           bullet.pos[0] > canvas.width) {
            bullets.splice(i, 1);
            i--;
        }
    }

    // Update all the enemies
    for(var i=0; i<enemies.length; i++) {
        enemies[i].pos[0] -= enemySpeed * dt;

        // Remove if offscreen
        if(enemies[i].pos[0] + enemies[i].sprite.size[0] < 0) {
            enemies.splice(i, 1);
            i--;
        }
    }

    // Update all the explosions
    for(var i=0; i<explosions.length; i++) {

        // Remove if animation is done
        if(explosions[i].sprite.done) {
            explosions.splice(i, 1);
            i--;
        }
    }
}







